#topButton {
  visibility:hidden;
  position: fixed;
  bottom: 20px;
  right: 30px;
  border: none;
  outline: none;
  background-color: #55198b;
  color: white;
  cursor: pointer;
  padding: 4px 7px;
  border-radius: 100%;
  font-size: 30px;
}

#topButton:hover {
  background-color: #000;
  transition: all ease-in-out 0.2s;
}
