.contact-margin-top {
    width: 90%;
    padding: 10px 10px;
    margin: 0px auto;
    margin-top: 4rem;
  }


.contact-div-main {
    display: flex;
}
.contact-div-main > * {
    flex: 1;
}




/*title+subtitle*/

.contact-title {
    font-size: 65px;
    font-weight: bold;
    text-align: center;
}

.contact-subtitle {
    text-transform: uppercase;
    text-align: center;
}


/*info*/

.contact-text-div {
    margin-top: 1.5rem;
    padding:20px;
    text-align: center;
}

.contact-detail,
.contact-detail-email {
    margin-top: 20px;
    font-size: 40px;
    font-weight: 400;
    color: #868e96;
    text-decoration: none;
}
.contact-detail:hover,
.contact-detail-email:hover {
    color: black;
    text-shadow: 2px 1px 2px #b5b5b5;
    transition: all 0.3s;
}




/*image*/

.contact-image-div img {
    max-width: 100%;
    height: auto;
    margin-right: 1.5rem;
    margin-top: -1rem;
}










/* Media Query */
@media (max-width: 1380px), (max-width: 768px) {
    .contact-title {
        font-size: 56px;
        font-weight: 400;
        text-align: center;
    }

    .contact-title {
        font-size: 40px;
        text-align: center;
        font-weight: bold;
    }

    .contact-subtitle {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }

    .contact-text-div {
        text-align: center;
        margin-top: 1.5rem;
    }

    .contact-detail,
    .contact-detail-email {
        font-size: 20px;
    }

    .contact-image-div {
        display: none;
    }
}
